import MasterPage from '../components/Master';
import fetch from 'isomorphic-unfetch';
const Index = (props) => (
  <MasterPage>
    <div className="row">
      <div className="col-12">
        <h2>Precio del bitcoin</h2>
        { console.log(props) }
      </div>
      <div className="col-md-8">
      <h2>Noticias sobre bitcoin</h2>
      </div>
      <div className="col-md-4">
      <h2>Proximos eventos bitcoin</h2>
      </div>
    </div>
  </MasterPage>
);

Index.getInitialProps = async () => {
  const precio = await fetch('https://pro-api.coinmarketcap.com/v1/cryptocurrency/quotes/latest?id=1', {headers: {'X-CMC_PRO_API_KEY': 'b54bcf4d-1bca-4e8e-9a24-22ff2c3d462c'}});
  const restPrecio = await precio.json();
  return {
    precioBitcoin: restPrecio
  }
}

export default Index;
